<template>
	<view>
		<tabTop left-icon="back" title="选择收款钱包地址" />
		<view class="container">
			<view class="listItemBox" v-for="(item,index) in addressData" :key='index' :index='index' @click="selectAddress(index)">
				<view class="listItem">
					<view>
						<view class="addressTxt colorTxt">{{item.addressName}}</view>
						<view class="addressDes">{{item.des}}</view>
					</view>
					<view>
						<image v-show="select==index" class="selectIcon" src="~@/static/right_icon.png"></image>
					</view>
				</view>
			</view>
			<view class="btnBox">
				<button class="btn addressTxt mb10" @click="addWallet">新建钱包地址</button>
				<button class="btn addressTxt" @click="next">下一步</button>
			</view>
		</view>
		<!-- 密码框 -->
		<view class="pwdModal" v-if="modalShow">
			<label class="close" @click="closeModal"></label>
			<view class="pwdCon">
				<view class="pwdTxt">请输入密码</view>
				<view>
					<input type="password" class="pwdInp">
				</view>
				<view class="pwdBtnBox">
					<button class="pwdBtn" @click="pwdSubmit">确定</button>
					<button class="pwdBtn" @click="closeModal">取消</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import tabTop from '@/components/tabTop.vue'
	export default {
		components: {
			tabTop
		},
		data() {
			return {
				select: -1,
				modalShow:false,
				modalType:1,
				addressData: [{
						addressName: '地址名称1',
						des: 'fdud7162Gse183',
					},
					{
						addressName: '地址名称2',
						des: 'fdud7162Gse183'
					}
				]
			}
		},
		methods: {
			// 新建钱包地址
			addWallet() {
				uni.navigateTo({
					url: "../editAddress/editAddress"
				})
			},
			// 选择收款钱包地址
			selectAddress(index) {
				let _this = this
				_this.select = index
			},
			// 下一步
			next(){
				let _this = this
				_this.modalShow = true
			},
			// 关闭密码框
			closeModal(){
				let _this = this
				_this.modalShow = false
			},
			// 导出助记词跳转
			pwdSubmit(){
				let _this = this
				let type = _this.modalType
				if(type == 1){
					uni.navigateTo({
						url:'./exportWord/exportWord'
					})
					_this.modalShow = false
				} else if(type == 2){
					uni.navigateTo({
						url:'./exportPrivateKey/exportPrivateKey'
					})
					_this.modalShow = false
				}
			}
		}
	}
</script>
<style>
	page {
		background-image: url(../../../static/bg.png);
		background-size: 100% 100%;
	}
</style>
<style scoped lang="scss">
	.colorTxt {
		color: #2DE9B7;
	}

	.listItem {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 100rpx;
		background-color: #1D2B49;
		padding: 0 34rpx;
		box-sizing: border-box;
		border-bottom: 2rpx solid #42527A;
	}

	.itemLeft {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}

	.addressTxt {
		font-size: 24rpx;
	}

	.addressDes {
		font-size: 18rpx;
		color: #8DA1C1;
	}

	.selectIcon {
		width: 34rpx;
		height: 34rpx;
		background-size: cover;
	}

	.btnBox {
		position: fixed;
		bottom: 83rpx;
		left: 50%;
		transform: translateX(-50%);
	}

	.mb10 {
		margin-bottom: 10rpx;
	}

	.btn {
		width: 560rpx;
		height: 66rpx;
		line-height: 66rpx;
		color: #fff;
		border-radius: 4rpx;
		background: linear-gradient(left, #2A65BC, #079D74);
	}
	.pwdModal{
		position: absolute;
		top: 350rpx;
		left: 94rpx;
		right: 94rpx;
		background: #075F4D;
		height: 350rpx;
		box-sizing: border-box;
		border-radius: 14rpx;
		z-index: 99;
		.close{
			display: inline-block;
			width: 20rpx;
			height: 2rpx;
			background: #fff;
			transform: rotate(45deg);
			position:absolute;
			top: 30rpx;
			right: 20rpx;
		}
		.close::after{
			content: '';
			width: 20rpx;
			height: 2rpx;
			background: #fff;
			transform: rotate(90deg);
			position: absolute;
		}
		.pwdCon{
			position: absolute;
			top: 60rpx;
			width: 100%;
			text-align: center;
			.pwdTxt{
				font-size: 30rpx;
				color: #fff;
			}
			.pwdInp{
				margin: 50rpx 30rpx 20rpx 30rpx;
				box-sizing: border-box;
				height: 66rpx;
				line-height: 166rpx;
				border: 2rpx solid #2DEAB7;
				border-radius: 4rpx;
				background: #0D3C3B;
				color: #fff;
			}
			.pwdBtnBox{
				display: flex;
				justify-content: space-between;
				margin: 0 30rpx;
				.pwdBtn{
					font-size: 24rpx;
					color: #fff;
					width: 200rpx;
					height: 66rpx;
					line-height: 66rpx;
					background: #0D3C3B;
					border: 2rpx solid #2DEAB7;
					border-radius: 4rpx;
				}
			}
		}
	}
</style>
